import uniPopup from "../../components/uni-ui/uni-popup/uni-popup.vue"
export default {
	name: "",
	components: {
		uniPopup
	},
	props: {},
	data() {
		return {
			user: {}
		}
	},
	methods: {
		open() {
			// 需要在 popup 组件，指定 ref 为 popup
			this.$refs.popup.open()
		},
		cancle() {
			// 需要在 popup 组件，指定 ref 为 popup
			this.$refs.popup.close()
		},
		see() {
			uni.previewImage({
				urls: [this.user.faceImage]
			});
		},
		chooseimage() {
			uni.chooseImage({
				sourceType: ['album'],
				success: res => {
					this.user.faceImage = res.tempFilePaths[0]
					this.$refs.popup.close()
					uni.request({
						url: `${this.$api}/user/modifyUserinfo?qq=122212489`,
						method: 'POST',
						header: {
							"headerUserId": this.user.id,
							"headerUserToken": this.user.userUniqueToken
						},
						data: {
							birthday: this.date,
							nickname: this.user.nickname,
							sex: this.user.sex,
							userId: this.user.id
						},
						success: res => {
							console.log(res);
							uni.setStorageSync('user', this.user)
						},
						fail: () => {},
						complete: () => {}
					});
				},
			})
		},
		editname() {
			uni.navigateTo({
				url: '/pages/editname/editname',
			});
		},
		edittime() {
			uni.navigateTo({
				url: '/pages/edittime/edittime',
			});
		},
		editsex() {
			uni.navigateTo({
				url: '/pages/editsex/editsex',
			});
		},
		preservation(){
			uni.showToast({
				title:'保存成功'
			})
			uni.switchTab({
				url: "/pages/me/me"
			})
		},
		out() {
			uni.request({
				url: `${this.$api}/user/logout?userId=${this.user.id}&qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					console.log(res);
					uni.removeStorageSync('user')
					uni.switchTab({
						url: "/pages/me/me"
					})
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		}
	},
	mounted() {

	},
	onLoad() {
		this.user = uni.getStorageSync('user')
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
